body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: #212121; }

.Typist .Cursor {
  display: inline-block; }
  .Typist .Cursor--blinking {
    opacity: 1;
    -webkit-animation: blink 1.4s linear infinite;
            animation: blink 1.4s linear infinite; }

@-webkit-keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.center {
  position: absolute;
  top: 45%;
  left: 50%;
  -webkit-transform: translate(-50%, -45%);
      -ms-transform: translate(-50%, -45%);
          transform: translate(-50%, -45%); }

.fade {
  opacity: 0;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.exit {
  position: absolute;
  right: 10px;
  top: 10px;
  opacity: 0.04;
  -webkit-transition: opacity 1s ease;
  -o-transition: opacity 1s ease;
  transition: opacity 1s ease; }
  .exit:hover {
    opacity: 1; }

.intro {
  text-align: center; }

.button-holder {
  display: inline-block;
  margin-left: auto;
  margin-right: auto; }

.intro-response {
  font-size: 6em;
  font-weight: bold; }

.button {
  background-color: #212121;
  color: #FAFAFA;
  font-weight: bold;
  text-align: center;
  font-size: large;
  border: 2px solid #212121;
  border-radius: 8px;
  text-transform: uppercase;
  padding: 10px 15px;
  margin: 5px;
  display: inline-block;
  cursor: pointer;
  -webkit-transition: font-size 0.5s ease, background-color 0.5s ease-out;
  -o-transition: font-size 0.5s ease, background-color 0.5s ease-out;
  transition: font-size 0.5s ease, background-color 0.5s ease-out; }
  .button:hover {
    font-size: x-large;
    background-color: #424242;
    border: 2px solid #424242; }



